import React, { useState } from 'react';
import { Person } from '../types/Person';
import { PersonRow } from './PersonRow';

type Props = {
  people: Person[],
};

export const PeoplePage: React.FC<Props> = ({ people }) => {
  const [filteredPeople, setFilteredPeople] = useState(people);

  const filterPeople = (event: React.ChangeEvent<HTMLInputElement>) => {
    const currentQuery = event.target.value;

    setFilteredPeople(people.filter(person => (
      person.name.includes(currentQuery)
      || person.motherName.includes(currentQuery)
      || person.fatherName.includes(currentQuery))));
  };

  return (
    <>
      <h1 className="subtitle is-3">People page</h1>
      <input
        className="input"
        type="text"
        onChange={filterPeople}
      />
      <table
        className="
        PeopleTable
        people_table
        table
        is-bordered
      "
      >

        <thead className="thead">
          <th>name</th>
          <th>sex</th>
          <th>born</th>
          <th>died</th>
          <th>mother</th>
          <th>father</th>
        </thead>

        <tbody>
          {filteredPeople.map(person => (
            <PersonRow key={person.slug} person={person} />
          ))}
        </tbody>

      </table>
    </>
  );
};
